.footer{width:100%;display:flex;align-items:center;justify-content:center;flex-direction:column}.footer .hr{width:100%;height:10px;border-top:2px solid var(--h-color);margin-bottom:2rem}.footer .img{width:15rem;height:4rem;margin-bottom:1rem}.footer .img img{-o-object-fit:cover;object-fit:cover;height:100%;width:100%}.footer .icons{display:flex;align-items:center;justify-content:center;gap:1rem;margin-bottom:3rem}.footer .icons a{padding:10px}.footer .icons a:hover svg{color:var(--teal);transform:scale(1.3)}.footer .icons a svg{height:25px;width:25px;transition:all .3s;color:var(--h-color)}